<template>
  <div class="login-content">
    <!-- 公司Logo -->
    <!-- <img class="shidi-logo" :src="require('@/assets/img/login/logo-login.png')" /> -->

    <!-- 登录 Form -->
    <div v-if="isDoubleCheck">
      <!-- 管理员双因子登录 -->
      <div class="error-msg">{{ errMsg }}</div>
      <form-login-double-check @submit="submit" ref='doubleCheckLogin' />
    </div>

    <el-tabs v-else
      v-model="loginType" class="login-form-tab" @tab-click="errMsg = ''">

      <!-- 密码登录 -->
      <el-tab-pane :label="$t('login.log_in_password')" name="login">
        <div class="error-msg">{{ errMsg }}</div>
        <form-login ref="formLogin" @submit="submit" />

        <div class="link">
          <!-- <router-link :to="{ name:'Subscribe' }"
            class="text-link">{{ $t('login.sign_up_for_updates') }}</router-link> -->
          <router-link :to="{ name:'PasswordReset' }">{{ $t('login.reset_password') }}?</router-link>
        </div>

        <!-- 提交表单按钮 -->
        <el-button type="primary" @click="submit">{{ $t('login.sign_in') }}</el-button>
      </el-tab-pane>

      <!-- 验证码登录 -->
      <el-tab-pane :label="$t('login.log_in_code')" name="loginByCode">
        <div class="error-msg">{{ errMsg }}</div>
        <form-login-by-code ref="codeLogin" @submit="submit"></form-login-by-code>
      </el-tab-pane>

    </el-tabs>

    <p class="sign-up-box">
      <router-link :to="{ name: 'Register' }"
        class="text-link">{{ $t('login.sign_up') }}</router-link> {{ $t('login.or') }}
      <router-link :to="{ name: 'Trial' }"
        class="text-link">{{ $t('login.free_trial') }}</router-link>
    </p>

    <!-- 虚拟账号被冻结时，登录限制时显示的弹框 -->
    <dialog-login-deny ref="dialogLoginDeny" />
  </div>
</template>

<script>
import { MessageBox } from 'element-ui';
import i18n from 'i18next';
import setLang, {
  languageNumber2lang,
} from '@/router/utils/set-lang';
import mixinLoading from '@/mixins/loading';
import { instance as history } from '@/utils/history';
import validEmail from '@/utils/validators/checkers/is-valid-email';
import axios from '@/utils/axios';

import { checkVerificationCodeMatching } from '@/api/email';
import envs from '@/utils/envs';
import { login, loginByCode, loginDoubleCheck } from '../../../loaders/context';
import DialogInvitationQuery from './components/DialogInvitationQuery';
import DialogLoginDeny from './components/DialogLoginDeny';
import FormLogin from './components/FormLogin';
import FormLoginByCode from './components/FormLoginByCode';
import FormLoginDoubleCheck from './components/FormLoginDoubleCheck';
import ChangePasswordWrapperVue from './components/ChangePasswordWrapper.vue';

export default {
  name: 'LoginContent',
  components: {
    FormLogin,
    FormLoginByCode,
    FormLoginDoubleCheck,
    DialogLoginDeny,
  },
  mixins: [mixinLoading],
  props: {
    inviteId: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: '',
    },
    redirect: {
      type: String,
      default: '/',
    },
    // 登录安全级别
    level: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      loginType: 'login',
      errMsg: '',
      isDoubleCheck: false,
    };
  },
  methods: {
    getAccount() {
      if (this.isDoubleCheck) {
        return this.$refs.doubleCheckLogin.form.account;
      }
      if (this.loginType === 'login') {
        return this.$refs.formLogin.form.account;
      }
      return this.$refs.codeLogin.form.account;
    },
    async submit(formData) {
      this.errMsg = '';
      try {
        // 校验表单是否通过；通过时返回form对象
        let form;
        if (this.isDoubleCheck) {
          // 管理员双因子登录
          form = formData;
          await checkVerificationCodeMatching({ verifyCode: form.code, email: form.account, type: 2 });
        } else if (this.loginType === 'login') {
          // 普通账号密码登录
          form = await this.$refs.formLogin.validateForm();
        } else {
          // 邮箱验证码登录
          // form = await this.$refs.codeLogin.validateForm();
          form = formData;
          await checkVerificationCodeMatching({ ...form, type: 2 });
          form.code = form.verifyCode;
          delete form.verifyCode;
        }

        this.startLoading();
        // 邮箱地址统一转化为小写
        if (validEmail(form.account)) form.account = form.account.toLowerCase();
        // 对登录接口的信息做数据结构处理
        const data = {
          ...form, level: this.level || 1, inviteId: this.inviteId, type: this.type || 0,
        };

        if (this.isDoubleCheck) {
          await loginDoubleCheck(data);
        } else if (this.loginType === 'login') {
          await login(data);
        } else {
          await loginByCode(data);
        }

        let { redirect } = this;

        this.endLoading();


        // 校验账号是否加入邀请的workspace状态的接口需要token，所以要先登录请求token
        // 这是设计上的错误；可以改为 打开链接页面时，就去请求校验是否过期，该接口不需要token

        // 询问是否加入邀请者发起的workspace
        let invitation; let isInvitationValid;
        const { type, inviteId } = this;
        if (inviteId) {
          // 由于邀请id只能生效一次，不能多次使用，所以在使用前要判断是否有效
          try {
            isInvitationValid = (await axios.get(`/meeting/member/invite/check?inviteId=${inviteId}`)).data;
          } catch (e) {
            if (e.code === 404 || (e.code === 0 && e.message === 'test')) {
              isInvitationValid = true;
            } else {
              throw e;
            }
          }
        }
        if (isInvitationValid) {
          invitation = await this.$Dialog.dialogify({
            is: DialogInvitationQuery,
            props: { inviteId, type },
          });
        }

        if (type === '2' && invitation) {
          redirect = `/file-list?appType=13&relateId=${invitation.inviter.groupAppId}`;
          localStorage.setItem('inviteWorkspaceId', '');
        }
        if (type === '4' && invitation) {
          redirect = `/up-meeting/home?meetingId=${invitation}`;
        }

        let lang;
        if (!envs.VUE_APP_COOPERATION.includes('anta')) {
          lang = languageNumber2lang((await axios({
            url: '/msgcenter/config/language/info',
            method: 'get',
          })).data);
        }


        if (redirect.indexOf('language') === -1 && lang) {
          redirect += `${redirect.includes('?') ? '&' : '?'}language=${lang}`;
        }

        // console.log(redirect);
        const parsedHistory = history.parse(redirect);
        // 如果重定向的页面还是登录页，
        if (parsedHistory.fullPathWithDomain.includes(envs.VUE_APP_LOGIN_URL)) {
          redirect = '/';
        }
        history.replace(parsedHistory);
      } catch (err) {
        this.endLoading();
        if (err.code === 131001) {
          // 密码错误，需要提示剩余次数，涉及到翻译，所以后端只在err.message中返回剩余次数（数字），前端根据code翻译
          err.message = i18n.t('login.error._131001', { num: err.message });
        } else if (err.code === 131019) {
          // 账号被锁定
          err.message = i18n.t('login.error._131019');
        } else {
          err.message = i18n.t(`public.code_msg._${err.code}`, err.message || i18n.t('login.system_error')); // AppError.MESSAGES[err.code];
        }
        this.errMsg = err.message;
        if (err.message === 'The user is invalid') {
          this.$refs.dialogLoginDeny.open();
        } else if (err.code === 131013) {
          this.errMsg = '';
          MessageBox.alert(i18n.t('public.code_msg._131013'), i18n.t('public.dialog.notice'));
        } else if (err.message === 'params invalidate: invited user already in your company') {
          this.errMsg = '';
          MessageBox.alert(i18n.t('login.exist_in_company'), i18n.t('public.dialog.notice'));
        } else if (err.code === 131028) {
          // 管理员登录，请使用双因子验证
          this.isDoubleCheck = true;
        } else if (err.code === 131034) {
          this.errMsg = i18n.t('login.reset_tip');
          const h = this.$createElement;
          await this.$confirm('', '', {
            confirmButtonText: i18n.t('login.reset'),
            message: h('div', { class: 'flex-vertical-center' }, [
              h('i', {
                class: 'el-icon-warning',
                style: {
                  fontSize: '22px',
                  color: '#FAAD14',
                  marginRight: '14px',
                },
              }),
              i18n.t('login.reset_tip'),
            ]),
          });
          await this.$injectDialog({
            is: ChangePasswordWrapperVue,
            props: {
              account: this.getAccount(),
            },
          });
          this.$message.success('修改成功');
        } else {
          this.$errHandler(err);
        }
      }
    },
  },
};
</script>

