<template>
  <el-form :model="form" :rules="rules" ref="form">
    <el-form-item prop="account">
      <el-input
        v-model.trim="form.account"
        autocomplete="off"
        prefix-icon="el-icon-user"
        :placeholder="$t('login.account_or_email')"
        clearable
        @keyup.enter.native="submit" />
    </el-form-item>

    <el-form-item prop="pass">
      <el-input
        v-model.trim="form.pass"
        autocomplete="off"
        prefix-icon="el-icon-lock"
        :placeholder="$t('login.password')"
        clearable
        show-password
        @keyup.enter.native="submit" />
    </el-form-item>
  </el-form>
</template>

<script>
import i18n from 'i18next';
import validator, { validateFormFields } from '@/utils/validators/rules-validation';

export default {
  name: 'LoginFormComponent',
  data() {
    return {
      form: {
        account: '',
        pass: '',
      },
      rules: {
        account: [
          validator.requirement(),
          // ...validator.charLengthRange(6, 25),
          // { pattern: /^[\u4E00-\u9FA5A-Za-z0-9_@.]+$/,
          // message: 'can not have special characters' },
        ],
        pass: [
          validator.requirement(),
          // ...validator.charLengthRange(6, 20),
        ],
      },
    };
  },
  methods: {
    submit() {
      this.$emit('submit');
    },
    // 校验表单是否通过规则；如果通过返回 form 对象
    async validateForm() {
      try {
        await validateFormFields(this.$refs.form);
        return this._formatFormData(this.form);
      } catch (err) {
        throw (err || new Error(i18n.t('login.form_val_fail')));
      }
    },
    _formatFormData(form) {
      Object.keys(form).forEach((prop) => {
        if (typeof form[prop] === 'string') {
          // 去掉首尾空格
          // 防止XSS攻击（html方面）
          form[prop] = form[prop]
            .trim()
            .replace(/</g, '&lt;')
            .replace(/>/g, '&gt;');
        }
      });
      return form;
    },
  },
};
</script>
