













































import { i18n, defaultLang } from '@/i18n';
import {
  defineComponent, ref, reactive, computed,
} from '@vue/composition-api';
import isFilled from '@/utils/validators/is-filled';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import { validateFormFields } from '@/utils/validators/rules-validation';
import { ElForm } from 'element-ui/types/form';
import {
  sendVerifyCodeByLogin,
} from '@/api/email';
import errHandler from '@/utils/error-handler';
import BaseCountDownButton from '@/components/base/CountDownButton.vue';

const rules = {
  account: validator2ElemRule((value: string) => {
    try {
      isFilled(value);
    } catch (e) {
      throw new Error(i18n.t('validator.not_empty'));
    }
  }),
  pass: validator2ElemRule((value: string) => {
    try {
      isFilled(value);
    } catch (e) {
      throw new Error(i18n.t('validator.not_empty'));
    }
  }),
  code: validator2ElemRule((value: string) => {
    try {
      isFilled(value);
    } catch (e) {
      throw new Error(i18n.t('validator.not_empty'));
    }
  }),
};

const FormLoginByCode = defineComponent({
  name: 'FormLoginByCode',
  components: {
    BaseCountDownButton,
  },
  props: {},
  setup(props, context) {
    const form = reactive<{
      account: string
      pass: string
      code: string
      [prop: string]: string
    }>({
      account: '',
      pass: '',
      code: '',
    });
    const formRef = ref<ElForm>();

    const disabledVerify = computed(() => {
      return form.account.length <= 0;
    });

    // 提交表单（触发validate事件通知到父组件）
    async function submit() {
      // trim所有的 from
      Object.keys(form).forEach((prop) => {
        form[prop] = form[prop].trim();
      });
      await validateFormFields(formRef.value!!);
      context.emit('submit', form);
      return form;
    }

    // 发送验证码到邮箱
    async function getVerificationCode() {
      try {
        await sendVerifyCodeByLogin(form.account);
      } catch (err) {
        errHandler(err);
      }
    }

    return {
      formRef,
      form,
      rules,
      submit,
      disabledVerify,
      getVerificationCode,
    };
  },
});
export default FormLoginByCode;
