<template>
  <div v-loading="isLoading">
    <figure class="flex-column flex-center">
      <img style="width:266px;height:200px;" src="@/assets/img/login/workspace-invited.png">
      <figcaption>
        <div v-if="type === '1'" class="figure-title flex-center">
          {{ inviter.companyName || inviter.account }}
        </div>
        <div v-else-if="type !== '4'" class="figure-title flex-center">
          <div class="iconfont-wrapper flex-center">
            <i class="iconfont icon-tool"></i>
          </div>
          {{ inviter.groupAppName }}
        </div>
      </figcaption>
    </figure>

    <p v-if="type === '1'" class="dialog-text">
      {{ $t('login.invite_accept_note', { name: inviter.userName || inviter.account }) }}
    </p>
    <p v-else-if="type === '4'" class='flex-column flex-vertical-center meeting-container'>
      <span class="flex">
        <span>{{ meetUpInvitationInfo.inviterName }}</span>
        <span style="color: #bbb;margin-left: 10px">{{$t('login.invite_to_meetup')}}</span>
      </span>
      <span class="flex-center flex-vertical-center" style="margin-top: 20px;">
        <div :style="{
          width:'30px',
          height:'30px'
        }">
          <base-image :src="meetUpInvitationInfo.logo" object-fit="scale-down"></base-image>
        </div>
        {{meetUpInvitationInfo.meetingName}}
      </span>
    </p>
    <p v-else class="dialog-text" style="text-align:center;"
      v-html="$t('login.invite_note', { name: inviter.userName })">
    </p>

    <div class="flex-center">
      <el-button size="small" @click="submit(2)">{{ $t('login.refuse') }}</el-button>
      <el-button size="small" type="primary" @click="submit(1)">{{ $t('login.accept') }}</el-button>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import { getInviterInfo, acceptInvitation } from '@/api/email';
import axios from '@/utils/axios';

export default {
  dialogify: {
    class: {
      'border-radius-10px': true,
    },
    props: {
      width: '360px',
      height: '400px',
      showClose: false,
      'close-on-click-modal': false,
      'close-on-press-escape': false,
      center: true,
    },
  },
  props: {
    // 1是邀请进公司，2-3是邀请进workspace，4是邀请进会议
    type: {
      type: String,
      required: true,
    },
    inviteId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      inviter: {
        companyName: '',
        account: '',
        groupAppName: '',
        userName: '',
      },
      meetUpInvitationInfo: {
        cooperationId: '',
        endTime: '',
        introduce: '',
        inviterId: '',
        meetingId: '',
        meetingName: '',
        startTime: '',
        inviterName: '',
        logo: '',
      },
      isLoading: false,
    };
  },
  created() {
    this._fetchInviterInfo();
  },
  methods: {
    async submit(status) {
      if (this.type === '4') {
        try {
          this.isLoading = true;
          await axios.post('/meeting/member/invite/handle', {
            inviteId: this.inviteId,
            status,
          });
          this.$emit('confirm', status === 1 && this.meetUpInvitationInfo.meetingId);
        } catch (e) {
          console.error(e.message);
          this.$message.error(e.message);
        } finally {
          this.isLoading = false;
        }
        return;
      }
      if (status === 2) {
        this.$emit('confirm', false);
        return;
      }
      try {
        this.isLoading = true;
        await acceptInvitation({ inviteId: this.inviteId, status });
        this.$emit('confirm', {
          inviter: this.inviter,
        });
      } catch (err) {
        if (err.code === 131023) {
          this.$emit('confirm', {
            inviter: this.inviter,
          });
          this.$message.success(i18n.t('login.exist_in_company'));
        } else if (err.code === 131022) {
          this.$emit('confirm', {
            inviter: this.inviter,
          });
          this.$message.success(i18n.t('login.exist_in_workspace'));
        } else {
          this.$emit('cancel', err);
        }
      } finally {
        this.isLoading = false;
      }
    },
    // 获取邀请发起者的信息
    async _fetchInviterInfo() {
      try {
        const res = (await getInviterInfo(this.inviteId)).data;
        if (this.type === '4') {
          this.meetUpInvitationInfo = res;
          const [first] = await (await axios.post('authcenter/user/im/info', [this.meetUpInvitationInfo.inviterId])).data;
          this.$set(this.meetUpInvitationInfo, 'inviterName', first.name);
        } else {
          this.inviter = res;
        }
      } catch (err) {
        this.$errHandler(err);
      }
    },
  },
};
</script>

