















import Vue from 'vue';
import FormChangePassword from '@/views/profile/info/components/form/FormChangePassword.vue';
import { axiosEncrypt } from '@/utils/axios';
import i18n from 'i18next';


export default {
  components: { FormChangePassword },
  props: {
    account: {
      type: String,
    },
  },
  dialogify: {
    props: {
      width: '440px',
      closeOnClickModal: false,
      closeOnPressEscape: false,
      showClose: false,
      title: i18n.t('login.change_password'),
    },
  },
  data() { return { isLoading: false }; },
  methods: {
    // 修改密码
    async handleValidateFormSuccess({ oldPass, pass }: { oldPass: string, pass: string }) {
      try {
        if (!this.$props.account && !this.$attrs.account) throw new Error('请提供account字段');
        this.isLoading = true;
        await axiosEncrypt({
          url: '/authcenter/user/password/update',
          method: 'POST',
          data: {
            account: this.account || this.$attrs.account,
            oldPass,
            newPass: pass,
          },
        });
        this.$emit('confirm');
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.isLoading = false;
      }
    },
  },
};
