<template>
  <el-button
    type="primary"
    :size="size"
    :disabled="disabledStatus"
    @click="countDown">
    {{ content }}
  </el-button>
</template>

<script>
export default {
  props: {
    // 倒计时长度
    seconds: {
      type: Number,
      default: 60,
    },
    size: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    defaultContent: {
      type: String,
      default: 'Count Down',
    },
    sendedContent: {
      type: String,
      default: 'Remain',
    },
  },
  data() {
    return {
      countDownNumber: 0,
      sendedAvailable: true,
      disabledStatus: this.disabled,
      timer: null,
    };
  },
  computed: {
    content() {
      return this.sendedAvailable ? this.defaultContent : `${this.sendedContent} ( ${this.countDownNumber} )`;
    },
  },
  watch: {
    seconds(value) {
      this.countDownNumber = value;
    },
    disabled(value) {
      this.disabledStatus = value;
    },
  },
  methods: {
    countDown() {
      if (this.sendedAvailable) {
        this.countDownNumber = this.seconds;
        this.sendedAvailable = false;
        this.$emit('send');

        this.timer = setInterval(() => {
          if (this.countDownNumber > 1) {
            this.countDownNumber--;
            this.disabledStatus = true;
          } else {
            this.sendedAvailable = true;
            this.disabledStatus = false;
            clearInterval(this.timer);
          }
        }, 1000);
      }
    },
  },
};
</script>
